<template>
    <div class="login">
        <div class="login-box">
            <div class="topbox fr" @click="goTuijian">先去体验</div>
            <div class="bodybox">
                <div class="logobox">
                    <img src="../assets/img/logo.svg" alt />
                </div>
                <div class="bodyinput-box">
                    <van-form>
                        <van-field
                            class="field"
                            v-model="userLoginInfo.phone"
                            name="手机号码"
                            placeholder="手机号码"
                        />
                        <van-field
                            class="field"
                            v-model="userLoginInfo.password"
                            :type="isShowPw ? 'text' : 'password'"
                            :right-icon="isShowPw ? 'eye-o' : 'closed-eye'"
                            name="密码"
                            placeholder="密码"
                            @click-right-icon="showeye"
                        />

                        <div class="submit-btn">
                            <van-button
                                round
                                block
                                native-type="submit"
                                @click="login"
                                >登&nbsp;&nbsp;录</van-button
                            >
                        </div>
                    </van-form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import "../assets/less/login.less";
import { utils } from "../assets/js/utils";
import { validForm } from "../assets/js/validForm";
import { setupCookie } from "../assets/js/setupCookie";
import { createNamespacedHelpers } from "vuex";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    data() {
        return {
            userLoginInfo: {
                phone: "",
                password: ""
            },
            isShowPw: false,
            ispopupShowPw: false,
            popupShow: false,
            registerInfo: {
                nickName: "",
                phone: "",
                password: ""
            }
        };
    },
    created() {
        //设置音乐组件位置
        this.setaudioxy(2);
    },
    methods: {
        ...musicActions(["setUserId", "setaudioxy"]),
        showeye() {
            this.isShowPw = !this.isShowPw;
        },
        popupshoweye() {
            this.ispopupShowPw = !this.ispopupShowPw;
        },
        showPopup() {
            this.popupShow = !this.popupShow;
        },

        //跳转到推荐
        goTuijian() {
            this.$router.push({ name: "Tuijian" });
        },

        //登录
        login() {
            let logininfo = this.userLoginInfo;
            let reg = /^1[3456789]\d{9}$/;
            if (logininfo.phone == "") {
                this.$notify({
                    type: "warning",
                    color: "#ee2e23",
                    background: "#fff",
                    duration: 700,
                    message: "手机号码不能为空"
                });
                return;
            } else if (!reg.test(logininfo.phone)) {
                this.$notify({
                    type: "warning",
                    color: "#ee2e23",
                    background: "#fff",
                    duration: 700,
                    message: "请填写正确的手机号码"
                });
                return;
            }

            //序列化参数
            let info = "";
            info = Object.assign({}, logininfo);
            let data = utils.queryString(info);
            // 访问
            this.axios({
                method: "POST",
                url: "/login/cellphone",
                data
            })
                .then(result => {
                    this.$notify({
                        type: "warning",
                        color: "#ee2e23",
                        background: "#fff",
                        duration: 700,
                        message: result.data.msg
                    });

                    //设置用户id
                    localStorage.setItem("ID", result.data.account.id);
                    this.userLoginInfo = "";

                    // 设置cookie
                    let cookievalue = result.data.cookie;
                    console.log("cookie---cookievalue :>> ", cookievalue);
                    console.log("设置cookie");
                    setupCookie.setupCookies(cookievalue);

                    this.$router.push({ name: "Tuijian" });
                })
                .catch(err => {});
        }
    }
};
</script>